const Tmdb = async ({Biblioteca, Genre,tipo}) => {
    const API_KEY = "5417af578f487448df0d4932bc0cc1a5";
    const API_BASE = 'https://api.themoviedb.org/3/';    
    const bucarSeasons = async (props)=>{
        let infEp = await props.infEp
        let tipo = await props.tipo
        let b = []
        for(let i = 1 ; i <= infEp.number_of_seasons; i++){
            await fetch(`${API_BASE}${tipo}/${infEp.id}/season/${i}?api_key=${API_KEY}&language=pt-BR`)
            .then((response)=>{return  response.json()})
            .then((data)=>{b.push(data.episodes.length)})   
            .catch((error)=>{console.log(error.message, tipo, 'season')})
        }
        return (await b)
    }
    
    const bucarId = async (props)=>{
        //busca por ID
        let id = props.id
        let tipo =  props.tipo
        let infEp = await fetch(`${API_BASE}${tipo}/${id}?api_key=${API_KEY}&language=pt-BR`)
        .then((response)=>{return response.json()})
        .then((data)=>{;return  data})   
        .catch(()=>{return null}) 
        
        if(!infEp || !infEp.poster_path){
            console.log('Erro Find')
            return null            
        }
        //busca por Epsodes
        let seasons = ''
        if(tipo==='tv')
        {seasons = bucarSeasons({infEp,tipo})}
        else{seasons = [1]}
        //console.log(await infEp)
        return({infEp,seasons})
    } 
    const mountArrySeries = async (props) =>{
        //Montar Arry
        
        let info = await props.infEp
        let ArrySeason = await props.seasons
        let genero = ''
        if(info.genres && info.genres[0]){genero = info.genres[0].name}
        else if(info.genres && info.genres[1]){genero = info.genres[1].name}
        else if(info.genres && info.genres[2]){genero = info.genres[2].name}       
        
        return{                    
            genre: genero,
            id: info.id,
            data : info.first_air_date,
            rating: info.vote_average,
            overview: info.overview,
            backdrop: info.backdrop_path,                
            nome: info.name,
            url: info.id,
            poster: info.poster_path,
            numberEpisodes: ArrySeason,
        };    
    }
    const mountArryFilmes = (props) =>{
        //Montar Arry
        let info = props.infEp
        let ArrySeason = props.seasons
        let genero = ''
        if(info.genres && info.genres[0]){genero = info.genres[0].name}
        else if(info.genres && info.genres[1]){genero = info.genres[1].name}
        else if(info.genres && info.genres[2]){genero = info.genres[2].name}     
        
        return{                    
            genre: genero,
            id: info.imdb_id,
            data : info.release_date,
            rating: info.vote_average,
            overview: info.overview,
            backdrop: info.backdrop_path,                
            nome: info.title,
            url: info.imdb_id,
            poster: info.poster_path,
            numberEpisodes: ArrySeason,
        };    
    }
    let ArryMod1 = []
    for(let i = 0; i<Biblioteca.length;i++){
        let id = Biblioteca[i].url        
        let busca = await bucarId({id,tipo})
        if(!busca){continue}
        let montarArry = []
        if(tipo==='tv'){montarArry = await mountArrySeries(busca)}
        else if(tipo==='movie'){montarArry = await mountArryFilmes(busca)}
        ArryMod1.push(montarArry);
    }
    
    const findTrending = await fetch(`https://api.themoviedb.org/3/trending/${tipo}/week?api_key=${API_KEY}`)
    .then((response)=>{return response.json()})
    .then((data)=>{return data.results.map(x=>{return x.id})})   
    .catch(()=>{return null}) 
    
    let ArryMod2 = []
    for(let i = 0; i<findTrending.length;i++){
        let id = findTrending[i]    
        let busca = await bucarId({id,tipo})
        if(!busca){continue}
        let montarArry = []
        if(tipo==='tv'){montarArry = await mountArrySeries(busca)}
        else if(tipo==='movie'){montarArry = await mountArryFilmes(busca)}
        ArryMod2.push(montarArry);
    }
    console.log('ArryMod2',ArryMod2)
    
    let b = await Promise.all(ArryMod1) 
    let c = Genre.map(x=>{return(b.filter(y => y.genre === x.name))})
    let d = []
    for(let i = 0; i<Biblioteca.length;i++){
        let x = Biblioteca[i]
        let filtrar = ArryMod2.filter(y=>y.id === x.url)
        if(filtrar.length>0){d.push(filtrar[0])}
    }
    console.log('d-'+tipo,d)
    let e = []
    if(d.length>0){
        e = [d.reverse()].concat(c)
    }
    else{e=c}
    console.log('Arry-'+tipo,e)
    //finalizando
    localStorage.setItem("Arry-"+tipo, await JSON.stringify(e)) 
    console.log('Buscou! Tipo: ',tipo)
    return (await e)
}    
export default Tmdb;