import '../Style/Video.css'
import { useState } from 'react';
import { useEffect } from 'react';
const Video = ({infoVideo,infoEpsode}) => {
    //const urlbase = "https://embed.querofilmeshd.co/tv/";
    //const [urlbase] = useState("https://embed.querofilmeshd.co/tv/");
    const [urlbase] = useState("https://player.uauflix.online/tv/");    
    const[urlSerie,seturlSerie] = useState('');
    const[descricao,setdescricao] = useState('');    
    const[urlFilme] = useState("https://player.uauflix.online/"+infoVideo.url);
    //const[urlFilme] = useState("https://embed.querofilmeshd.co/"+infoVideo.url);
    //const[urlFilme] = useState("https://pyronart5.ml/embed/"+infoVideo.url);    
    
    useEffect(() => {
        if(infoEpsode){
            console.log(infoVideo.nome,infoEpsode)
            seturlSerie(urlbase+infoVideo.url+infoEpsode.urls[0])
            setdescricao(infoEpsode.names[0])
            if(localStorage.getItem(infoVideo.nome)){
                let ler =JSON.parse(localStorage.getItem(infoVideo.nome))
                seturlSerie(urlbase+infoVideo.url+infoEpsode.urls[ler])
                setdescricao(infoEpsode.names[ler])
            } 
        }
    }, [urlbase,infoVideo,infoEpsode]);
    
    const contador = (props)=>{
        let contar = 0
        let rever = JSON.parse(localStorage.getItem(infoVideo.nome))
        console.log('rever: ',rever)
        contar = rever+props
        if(contar<0){contar =0}
        else if(contar>infoEpsode.urls.length-1){contar = infoEpsode.urls.length-1}
        console.log('contar: ',contar)
        seturlSerie(urlbase+infoVideo.url+infoEpsode.urls[contar])
        setdescricao(infoEpsode.names[contar])
        localStorage.setItem(infoVideo.nome, JSON.stringify(contar))    
    }
    
    if(!infoEpsode){
        return(
            <>
            <iframe sandbox="allow-scripts  allow-same-origin" title="Mflix" autoplay allowFullScreen scrolling="no" frameborder="0" src={urlFilme}></iframe>
            </>
            )
        }
        else{
            return(
                
                <div>
                <div className='Comands'>
                <h4>{descricao}</h4>
                <button type='button' onClick={()=>{contador(-1)}}>Back</button>
                <button type='button' onClick={()=>{contador(1)}}>Next</button>                
                </div>
                
                <iframe sandbox="allow-scripts  allow-same-origin" title="Mflix" allowFullScreen autoplay  scrolling="no" frameborder="0" src={urlSerie}></iframe>
                </div>
                )
            }     
        }
        export default Video;