import './Style/App.css';
import React from 'react';
import { useEffect } from 'react';
import { useState } from 'react';
import Series from './Components/Series';
import Filmes from './Components/Filmes';
import Header from './Components/Header';
import Tmdb from './Components/Tmdb';
import {GenresSeries} from './Dados/GenresSeries';
import {GenresFilmes} from './Dados/GenresFilmes';
import {BibliotecaSerie} from './Dados/BibliotecaSerie';
import {BibliotecaFilmes} from './Dados/BibliotecaFilmes';
const App = () => {
  
  const[type,SetType] = useState(JSON.parse(localStorage.getItem('Type')))
  const[infosSeries,setnfosSeries] = useState(null)
  const[infosMovie,setnfosMovie] = useState(null)
  
  window.open = function (url, windowName, windowFeatures) {
    alert('not opening a window: '+windowName);
} 
  
  const limparLocalStory = (props) =>{
    if(JSON.parse(localStorage.getItem('versao')) < props){localStorage.removeItem("Arry-movie");localStorage.removeItem("Arry-tv")}
    localStorage.setItem('versao', JSON.stringify(props))   
  }  
  const typeDefine = (props)=>{
    SetType(props)
    localStorage.setItem('Type', JSON.stringify(props))
  }
  const TmdbSerie = async ()=>
  {    
    BibliotecaSerie.reverse().splice(300)
    let Biblioteca = BibliotecaSerie
    let Genre = GenresSeries
    let tipo = 'tv'    
    //-----------
    let buscarlocal = JSON.parse(localStorage.getItem("Arry-"+tipo))
    console.log('buscarlocal-'+tipo,buscarlocal)
    if(buscarlocal){
      if(buscarlocal[0][0].length===0){
        let busca = await Tmdb({Biblioteca, Genre,tipo})
        setnfosSeries(busca)
        alert('tava vazio')
      }
      else{setnfosSeries(buscarlocal);}
    }
    else {
      let busca = await Tmdb({Biblioteca, Genre,tipo})
      setnfosSeries(busca)     
    }
  }
  const TmdbFilme = async ()=>
  {
    BibliotecaFilmes.reverse().splice(300).reverse()
    let Biblioteca = BibliotecaFilmes
    let Genre = GenresFilmes
    let tipo = 'movie'
    //-----------    
    let buscarlocal = JSON.parse(localStorage.getItem("Arry-"+tipo))
    console.log('buscarlocal-'+tipo,buscarlocal)
    if(buscarlocal){
      if(buscarlocal[0][0].length===0){
        let busca = await Tmdb({Biblioteca, Genre,tipo})
        setnfosMovie(busca)
        alert('tava vazio')
      }
      else{setnfosMovie(buscarlocal);}
    }
    else {
      let busca = await Tmdb({Biblioteca, Genre,tipo})
      setnfosMovie(busca)      
    }
  }
  useEffect(() => { 
    let versao = 7
    limparLocalStory(versao)
    TmdbSerie()   
    TmdbFilme()    
  },[]);
  
  return (
    <div className="App">
    {<Header infosSeries={infosSeries} typeDefine={typeDefine} infosMovie={infosMovie}/>}
    {!type && <Filmes infos={infosMovie}/>}
    {type && <Series infos={infosSeries}/>}
    {!infosMovie &&
      <img src='https://www.hibrido.com.br/wp-content/uploads/2018/03/artigo2.gif' alt={'carregando'} style={{filter: 'invert(1)',objectFit: 'cover',width:'100%',height:'99.49vh',display: 'absolute',top:'0px',left:'0px'}}/>
    }  
    </div>
    )
  }
  export default App;